import axios from 'axios'; //引入axios
// import localStorage from '../local-store'
import {
    secret
} from "@/util/secret";
import qs from 'qs';
import router from '../router'
import store from '../store/index';
import config from './config';

import { Message } from 'element-ui';

const { noGifArr, wihteList, request_json, request_file } = config;

axios.interceptors.request.use(function(config) {

    //console.log(config)


    if (config.method === 'post' && wihteList.indexOf(config.url) >= 0) {
        let secretParams = '';
        //secretParams.publicKey = store.state.publicKey;//localStorage.get('publicKey');
        //console.log('secretParams.publicKey----' + secretParams.publicKey)
        secretParams = secret.EncryptParams(config.data.params);
        // console.log('secretParams-----')
        // console.log(secretParams)
        config.data = secretParams;
    }


    let contentType;
    if (request_file.indexOf(config.url) > -1) {
        contentType = 'multipart/form-data';
        // console.log(config)
    } else if (request_json.indexOf(config.url) > -1) {
        contentType = 'application/json';
    } else {
        contentType = 'application/x-www-form-urlencoded';
    }

    let lang = window.localStorage.getItem('lang');
    if (lang == 'zh_hk') {
        lang = 'zh_hk';
    } else if (lang == 'en') {
        lang = 'en_us';
    } else {
        lang = 'zh_cn';
    }

    config.headers = {
            'content-type': contentType,
            'Authorization': store.state.token,
            'locale': lang
        }
        //zh_cn,zh_hk,en_us

    if (config.url == 'proxyUrl/wallet/v1/user/login')
        delete config.headers['Authorization']

    return config;
}, function(error) {
    return Promise.reject(error);
});

axios.interceptors.response.use(response => {
    // console.log(response)
    if (response.data.code == 401) {
        Message.error(('lang') == 'en' ? 'please sign in' : '请登录');
        store.commit('setUserInfo', null);
        store.commit('setToken', null);
        window.localStorage.removeItem('vuex');

        setTimeout(() => {
            router.push({
                path: '/login',
                query: {
                    redirect: router.currentRoute.fullPath
                }
            });
        }, 500);

        setTimeout(() => {
            location.reload();
        }, 800);

    } //end if
    return response.data;
}, function(error) {
    return Promise.reject(error);
});


export const get = (data, that) => {
        return new Promise(function(resolve, reject) {
            axios({
                    method: 'GET',
                    url: data.url,
                    params: data.params
                })
                .then(res => {
                    if (res.code == 200) {
                        resolve(res);
                    } else {
                        // console.log('错误/////')
                        // console.log(res)
                        setTimeout(() => {
                            if (res.message) {
                                Message.error(res.message);
                            }
                        }, 500);
                        reject(res);
                    }
                })
                .catch(err => {
                    reject(err);
                    console.error(err);
                    setTimeout(() => {
                        Message.error(window.localStorage.getItem('lang') == 'en' ? 'Network Error' : '网络错误');
                    }, 500);
                })
        })
    } //get

export const post = (data, ...reset) => {
        return new Promise(function(resolve, reject) {
            axios({
                    method: 'POST',
                    url: data.url,
                    data: data.params
                })
                .then(res => {
                    if (res.code == 200) {
                        resolve(res);
                    } else {
                        setTimeout(() => {
                            Message.error(res.message);
                        }, 500);
                        reject(res);
                    }
                })
                .catch(err => {
                    reject(err);
                    console.error(err);
                    setTimeout(() => {
                        Message.error(window.localStorage.getItem('lang') == 'en' ? 'Network Error' : '网络错误');
                    }, 500);
                })
        })
    } //get

export default {
    get,
    post
}


