const proxyUrl = process.env.NODE_ENV == 'development' ? 'proxyUrl' : 'proxyUrl';
export default {
    urlPath: proxyUrl + '/wallet/v1', //线上路由
    proxyUrl: proxyUrl, //线上路由
    noGifArr: [
        proxyUrl + '/api/list/order',
        proxyUrl + '/api/get/totalInfo',
        proxyUrl + '/wallet/v1/user/log'
    ], //不显示加载框路由
    wihteList: [
        proxyUrl + '/order/exchange',
    ], //加密名单
    request_json: [
        proxyUrl + '/wallet/v1/user/register',
        proxyUrl + '/wallet/v1/user/login',
        proxyUrl + '/wallet/v1/user/withdraw',
        proxyUrl + '/wallet/v1/user/transfer'
    ], //application/json方式请求接口
    request_file: [
        proxyUrl + '/wallet/v1/user/headUpload'
    ]
}


