import Vue from 'vue'
import Router from 'vue-router'

const notfound = resolve => require(['@/view/notfound'], resolve)
const home = resolve => require(['@/view/home'], resolve)
const exchange = resolve => require(['@/view/exchange'], resolve)
const login = resolve => require(['@/view/login'], resolve)
const register = resolve => require(['@/view/register'], resolve)
const forgetPass = resolve => require(['@/view/forgetPass'], resolve)
const userCenter = resolve => require(['@/view/userCenter'], resolve)
const userAssets = resolve => require(['@/view/userAssets'], resolve)
const userHistory = resolve => require(['@/view/userHistory'], resolve)
const userInOut = resolve => require(['@/view/userInOut'], resolve)
const userSafe = resolve => require(['@/view/userSafe'], resolve)
const userSetting = resolve => require(['@/view/userSetting'], resolve)
const userShare = resolve => require(['@/view/userShare'], resolve)
const userAssetsList = resolve => require(['@/view/userAssetsList'], resolve)
const userShareList = resolve => require(['@/view/userShareList'], resolve)
const help = resolve => require(['@/view/help'], resolve)
const userWithdrawList = resolve => require(['@/view/userWithdrawList'], resolve)
const exchangeDetail = resolve => require(['@/view/exchangeDetail'], resolve)
const helpDetail = resolve => require(['@/view/helpDetail'], resolve)
const userOrderFreedList = resolve => require(['@/view/userOrderFreedList'], resolve)
const ecology = resolve => require(['@/view/ecology'], resolve)



Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            name: 'home',
            component: home,
            meta: {
                title: "home"
            }
        },
        {
            path: '/exchange',
            name: 'exchange',
            component: exchange,
            meta: {
                title: "exchange"
            }
        },
        {
            path: '/ecology',
            name: 'ecology',
            component: ecology,
            meta: {
                title: "ecology"
            }
        },
        {
            path: '/exchangeDetail',
            name: 'exchangeDetail',
            component: exchangeDetail,
            meta: {
                title: "exchangeDetail"
            }
        },
        {
            path: '/login',
            name: 'login',
            component: login,
            meta: {
                title: "login"
            }
        },
        {
            path: '/register',
            name: 'register',
            component: register,
            meta: {
                title: "register"
            }
        },
        {
            path: '/forgetPass',
            name: 'forgetPass',
            component: forgetPass,
            meta: {
                title: "forgetPass"
            }
        },
        {
            path: '/help',
            name: 'help',
            component: help,
            meta: {
                title: "help",
                keepAlive: true
            }
        },
        {
            path: '/helpDetail',
            name: 'helpDetail',
            component: helpDetail,
            meta: {
                title: "helpDetail"
            }
        },
        {
            path: '/userCenter',
            name: 'userCenter',
            redirect: '/userCenter/assets',
            component: userCenter,
            meta: {
                title: "userCenter"
            },
            children: [{
                    path: '/userCenter/assets',
                    component: userAssets,
                    meta: {
                        title: "用户资产",
                        requireAuth: true
                    }

                },
                {
                    path: '/userCenter/inOut',
                    component: userInOut,
                    meta: {
                        title: "用户充提",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/history',
                    component: userHistory,
                    meta: {
                        title: "历史记录",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/userOrderFreedList',
                    component: userOrderFreedList,
                    meta: {
                        title: "释放记录",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/share',
                    component: userShare,
                    meta: {
                        title: "推荐分享",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/safe',
                    component: userSafe,
                    meta: {
                        title: "安全设置",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/setting',
                    component: userSetting,
                    meta: {
                        title: "账号设置",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/userAssetsList',
                    component: userAssetsList,
                    meta: {
                        title: "资产明细",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/userWithdrawList',
                    component: userWithdrawList,
                    meta: {
                        title: "提现记录",
                        requireAuth: true
                    }
                },
                {
                    path: '/userCenter/userShareList',
                    component: userShareList,
                    meta: {
                        title: "分享记录",
                        requireAuth: true
                    }
                }

            ]
        },
        {
            path: '*',
            name: 'notfound',
            component: notfound,
            meta: {
                title: "404"
            }
        }
    ]
})


