import {
    get,
    post
} from "./fetch"; //引用fetch.js
import qs from 'qs';
import config from './config';
const { proxyUrl } = config;

export function login(params) { //登陆
    return post({
        url: `${proxyUrl}/wallet/v1/user/login`,
        params
    })
} //login

export function getUserInfo(params) { //获取用户信息
    return get({
        url: `${proxyUrl}/wallet/v1/user/info`,
        params: qs.stringify(params)
    })
} //getUserInfo

export function getTickerList(params) { //获取行情列表
    return get({
        url: `${proxyUrl}/wallet/v1/user/ticker/list`
    })
} //getTickerList


