// 内部资料
// https://shimo.im/docs/wlEjNdjJliIfoNmu/read
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index';

import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
import './assets/theme/index.css'

Vue.use(ElementUI);

import {
    Check_browser,
    timerExchange,
    toFixed_4,
    toFixed_num,
    timestampToTime,
    roleType
} from './util';

import './util/EasyJSBridge.js';
Vue.prototype.easyJSBridge = window.EasyJSBridge.create("android", "ios", [
    // 可通用，用户异步操作，如移动端需要网络请求时可传入callback以供移动端调用
    "notify"
]);


import version from '../version.json'
Vue.prototype.version = version.versionName;

import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

import qs from 'qs';

Check_browser(); //检查浏览器是否支持本地存储

import VueI18n from 'vue-i18n' //多语言
Vue.use(VueI18n);

import language from './language';

const i18n = new VueI18n({
    locale: 'zh_cn', // 语言标识
    messages: {
        'zh_hk': language.zh_hk, // 中文语言包
        'zh_cn': language.zh_cn, // 中文语言包
        'en': language.en // 英文语言包
    }
})

//import VConsole from 'vconsole'; // 手机控制台插件。打包上线记得注释掉再打包 push online
//new VConsole()

import VuePageTransition from 'vue-page-transition'
Vue.use(VuePageTransition)

import {
    Toast
} from 'vant'

window.Toast = Toast;
window.ToastB = x => Toast({ message: x, position: 'bottom' });

import 'vant/lib/index.css';
import './styles/index.scss';

Vue.prototype.goback = () => {
    router.go(-1);
}
Vue.prototype.gopage = (path) => {
    router.push(path);
}
Vue.prototype.gopage_re = (path) => {
    router.replace(path);
}
Vue.prototype.openLink = (url) => {
    window.open(url);
}
Vue.prototype.roleType = (type) => {
    return roleType(type);
};

Vue.prototype.lang_actions = [{
    name: "简体中文",
    type: "zh_cn"
}, {
    name: "English",
    type: "en_us"
}, {
    name: "한국어",
    type: "ko_kr"
}, {
    name: "日本語",
    type: "ja_jp"
}]

Vue.prototype.blur_event = () => {
    window.scrollTo(0, 0);
    //window.scrollTo(0, document.documentElement.clientHeight);
}

Vue.prototype.lang = 'zh_cn';

Vue.prototype.set_lang = (type) => {
    i18n.locale = type;
    localStorage.setItem('lang', type);
    Vue.prototype.lang = type;
}

Vue.prototype.toFixed_4 = (num) => {
    return toFixed_4(num)
}

Vue.prototype.toFixed_num = (val, num = 8) => {
    return toFixed_num(num, val)
}


Vue.prototype.timerMD = (date) => {
    return timerExchange(date)
}

Vue.prototype.timestampToTime = (date) => {
    return timestampToTime(date)
}

Vue.prototype.setPopup = (data) => {
    //store.mutations.setPopup(data);
    store.commit('setPopup', data);
}


Vue.prototype.setImgUrl = (key) => {
    return (`proxyUrl/wallet/img/${key}`)
}


Vue.prototype.qsParams = (params) => {
    return qs.stringify(params)
}
Vue.prototype.parseParams = (params) => {
    return qs.parse(params)
}


Vue.config.productionTip = false
Vue.filter('dateFilter', function(value) {
    return new Date(parseInt(value) * 1000).toLocaleString();
})


/* eslint-disable no-new */
//const requireAuth = [] // 需要登登录跳转
//requireAuth.indexOf(to.path) >= 0 || 

router.beforeEach((to, from, next) => {
    window.scrollTo(0, 0);
    NProgress.start();
    let isLogin = store.state.token;
    if (to.meta.requireAuth) {
        if (isLogin) {
            next()
        } else {
            next()
            if (to.path === '/login') { //这就是跳出循环的关键
                next()
            } else {
                next()
                next('/login')
            }
        }
        next()
    } else {
        if (isLogin && (to.path === '/login' || to.path === '/register')) { //登录状态，登录注册跳转首页
            next()
        } else {
            next()
        }

    }
});

router.afterEach(transition => {
    NProgress.done();
})

new Vue({
    data() {
        return {

        }
    },
    el: '#app',
    router,
    store,
    i18n,
    components: {
        App
    },
    template: '<App/>',
    mounted: function() {
        var lang = localStorage.getItem('lang') || navigator.language;
        // console.log(lang)
        if (lang == 'zh_hk') {
            this.$i18n.locale = window.lang = 'zh_hk';
        } else if (lang.indexOf('en') >= 0) {
            this.$i18n.locale = window.lang = 'en';
        } else {
            this.$i18n.locale = window.lang = 'zh_cn';
        }
    }
})


