<template>
  <div id="app" class="bg_color walletView">
    <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        transitionName: 'transitionRouter',
        noEffect: ['/userCenter', '/assets', '/home', '/leaderHome', '/proxyHome', '/merchantHome']
      }
    },
	methods:{
		getStatus(){//主导航界面不加入动画
			return this.noEffect
		}//getStatus
	},
    watch: {
      '$route'(to, from) {
        const toDepth = to.path.split('/').length
        const fromDepth = from.path.split('/').length
        this.transitionName = toDepth < fromDepth ? 'transitionRouter' : 'transitionRouter'
      }
	},
    name: 'app'
  }

</script>

<style lang="scss">
  .transitionRouter-enter-active,
  .transitionRouter-leave-active {
    transition: all 0.25s;
    -webkit-transition: all 0.25s;
    opacity: 1;
    transform: scale(1);
    -webkit-transform: scale(1);
  }

  .transitionRouter-enter,
  .transitionRouter-leave {
    // transition: all 0.25s;
    // -webkit-transition: all 0.25s;
    opacity: 0;
    transform: scale(.95);
    -webkit-transform: scale(.95);
  }

  #app {
    min-height: 100vh;
    box-sizing: border-box;
  }

</style>



